import { inject, injectable } from 'inversify'
import * as _ from 'lodash'
import { LoggerService } from '@nestjs/common'
import { PrismaSchemaService } from '../schema/prismaSchema.service'
import { PrismaClientSymbol } from '../../interfaces/types'
import { matchPath, toModelName } from '../../utils/matchPath'
import { AuthService } from '../auth/auth.service'

/*
todo:  reference_type  nest
e.g. Customer#weixinProfile  Order#customerId  nest 
 */
@injectable()
export class DataService {
  private readonly logger

  constructor(
    // todo: prisma 
    @inject(PrismaClientSymbol) private prisma: any,
    @inject(PrismaSchemaService) private prismaSchemaService: PrismaSchemaService,
    @inject(AuthService) private auth: AuthService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(DataService.name)
  }

  async get(pathname: string, query: Record<string, any>) {
    const findParamRet = this.prismaSchemaService.toFindParam(pathname, query)
    if (_.isEmpty(findParamRet)) {
      return {}
    }
    const { resource, action, param } = findParamRet
    // todo: id 
    if (param.where.id) {
      param.where.id = await this.parseId(resource, param.where.id)
      this.logger.log('param.where:' + JSON.stringify(param.where))
    }
    return this.prisma[resource][action](param)
  }

  async put(path: string, values: any) {
    const matchRet = matchPath(path)
    const { resource, id } = matchRet[matchRet.length - 1]
    const nid = await this.parseId(resource, id as string)
    values.id = nid

    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    const ret = await this.prisma[resource].update({
      where: { id: nid },
      data: values,
    })
    this.logger.log(`uid ${this.auth.uid} PUT path: ${path}, values: ${JSON.stringify(values)}`)
    return ret
  }

  async post(path: string, values: any) {
    const matchRet = matchPath(path)
    const { resource } = matchRet[matchRet.length - 1]
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    const ret = await this.prisma[resource].create({
      data: values,
    })
    this.logger.log(`uid ${this.auth.uid} POST path: ${path}, values: ${JSON.stringify(values)}`)
    return ret
  }

  async remove(path: string) {
    const matchRet = matchPath(path)
    const { resource, id } = matchRet[matchRet.length - 1]
    let nid: string | number
    if (id == null) {
      throw new Error(`remove ${resource}, id null`)
    } else {
      nid = await this.parseId(resource, id as string)
    }
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    const ret = await this.prisma[resource].update({
      where: {
        id: nid,
      },
      data: {
        isDeleted: true,
      },
    })
    this.logger.log(`uid ${this.auth.uid} DELETE path: ${path}`)
    return ret
  }

  private async parseId(resource: string, id: string) {
    const modelName = toModelName(resource)
    const dmmf = await this.prisma._getDmmf()
    const idField = dmmf.modelMap[modelName].fields.find((item: any) => item.name === 'id')
    const nid = idField.type === 'Int' ? parseInt(id) : id
    this.logger.log(`id: ${id}, type: ${idField.type}`)
    return nid
  }
}
