import { Body, Controller, Delete, Get, HttpCode, Post, Put, Req, UseGuards } from '@nestjs/common'
import * as express from 'express'
import { DataService } from '@flowda-projects/flowda-shared'
import { UserJwtAuthGuard } from '../user/userJwtAuth.guard'

@Controller('data/*')
@UseGuards(UserJwtAuthGuard)
export class DataController {
  constructor(private service: DataService) {}

  @Get('')
  async get(@Req() req: express.Request) {
    return this.service.get(req.params[0], req.query)
  }

  @Put('')
  async put(@Req() req: express.Request, @Body() values: any) {
    return this.service.put(req.params[0], values)
  }

  @Post('')
  async post(@Req() req: express.Request, @Body() values: any) {
    return this.service.post(req.params[0], values)
  }

  @Delete('')
  @HttpCode(200)
  async remove(@Req() req: express.Request) {
    return this.service.remove(req.params[0])
  }
}
