export interface IResourceSchema {
  schema_name: string
  name: string
  slug: string
  primary_key: string
  display_column: string
  display_name: string | null
  display_primary_key: boolean
  searchable_columns: string[]
  columns: Partial<IResourceColumnSchema>[]
  associations: IResourceAssociationSchema[]
}

export interface IResourceAssociationSchema {
  foreign_key: string
  /**
   * Prisma Model 
   */
  model_name: string
  primary_key: string
  /**
   * Prisma 
   */
  name: string
  slug: string
  display_name: string
  schema_name: string
}

export interface IResourceColumnSchema {
  name: string
  access_type: 'read_only'
  column_type: 'reference' | 'string' | 'tag' | 'integer'
  format: {
    select_options: string[]
  }
  reference: any
  display_name: string | null
  validators: Array<
    | {
        required: boolean
      }
    | {
        format: string
        message: string
      }
  >
}
