import { inject, injectable } from 'inversify'
import { PrismaClientSymbol } from '@flowda-projects/flowda-shared'
import * as db from '@prisma/client-wms'
import axios from 'axios'
import * as _ from 'lodash'
import { WMS_ENV } from '../lib/wms-env'
import { LoggerService } from '@nestjs/common'

@injectable()
export class TaskService {
  private readonly logger

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(TaskService.name)
  }

  async start(key: string, uid: string) {
    const res = await axios.post(WMS_ENV.C7_REST_URL + `/process-definition/key/${key}/start`, {
      'variables': {
        'uid': {
          'value': uid,
          'type': 'Integer',
        },
      },
      'businessKey': 'myBusinessKey',
    })
  }

  async getTask(taskId: string) {
    const res = await axios.get(WMS_ENV.C7_REST_URL + `/task/${taskId}`)
    const res2 = await axios.get(WMS_ENV.C7_REST_URL + `/task/${taskId}/form-variables`)
    res.data.variables = JSON.stringify(res2.data)
    return res.data
  }

  /**
   * @param taskId
   *
   * 1.  taskId -> formKey
   * 2.  formKey -> form schema  view model
   * 3.  form schema  onInit  onComplete
   */
  async getTaskForm(taskId: string) {
    const res = await axios.get(WMS_ENV.C7_REST_URL + `/task/${taskId}`)
    const taskDefinitionKey = res.data.taskDefinitionKey
    const res2 = await axios.get(WMS_ENV.C7_REST_URL + `/task/${taskId}/form-variables`)
    this.logger.log(res2.data)
    const ret = await this.prisma.taskFormRelation.findUnique({
      where: {
        taskDefinitionKey,
      },
    })
    if (ret) {
      return {
        taskId: taskId,
        taskDefinitionKey: taskDefinitionKey,
        formKey: ret.formKey,
        variables: res2.data,
      }
    }
    return undefined
  }

  async complete(taskId: string, body: any) {
    const { resource, data } = body
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    const ret1 = await this.prisma[_.lowerFirst(resource)].create({
      data: data,
    })
    try {
      const ret2 = await axios.post(
        WMS_ENV.C7_REST_URL + `/task/${taskId}/complete`,
        {},
        {
          headers: {
            'Content-Type': 'application/json',
            Accept: 'application/json',
          },
        },
      )
    } catch (e) {
      this.logger.error(e)
    }

    return {
      taskId,
      resource,
      data,
      ret1,
    }
  }
}
