import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'

@injectable()
export class AuthService {
  private readonly logger

  constructor(@inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService) {
    this.logger = loggerFactory(AuthService.name)
  }

  uid: string | null = null

  setUID(uid: string | null) {
    if (uid == null) {
      this.logger.log(`set uid ${this.uid} to null`)
    } else {
      if (this.uid == null) this.logger.log(`set uid ${uid}`)
    }
    this.uid = uid
  }
}
