import { PassportStrategy } from '@nestjs/passport'
import { ExtractJwt, Strategy } from 'passport-jwt'
import { Inject, Injectable, Logger } from '@nestjs/common'
import { WMS_ENV } from '@flowda-projects/wms-services'
import { AuthService } from '@flowda-projects/flowda-shared'

@Injectable()
export class UserJwtStrategy extends PassportStrategy(Strategy, 'userJwt') {
  private logger = new Logger(UserJwtStrategy.name)

  constructor(@Inject(AuthService) private auth: AuthService) {
    super({
      jwtFromRequest: ExtractJwt.fromAuthHeaderAsBearerToken(),
      ignoreExpiration: false,
      secretOrKey: WMS_ENV.ACCESS_TOKEN_SECRET,
    })
  }

  async validate(payload: any) {
    this.logger.log(`expires at ${new Date(payload.exp0)}`)
    this.auth.setUID(payload.uid)
    return payload
  }
}
