export const PrismaZodSchemaSymbol = Symbol.for('PrismaZodSchema')
export const CustomZodSchemaSymbol = Symbol.for('CustomZodSchema')

export class CustomError extends Error {
  public message

  constructor(code: number, message: string, extra?: any) {
    super(JSON.stringify({ code: code, message }))
    this.message = JSON.stringify({ code, message, extra })
  }
}
