import { inject, injectable } from 'inversify'
import { PrismaClientSymbol } from '@flowda-projects/flowda-shared'
import { LoggerService } from '@nestjs/common'
import * as db from '@prisma/client-wms'
import { accessibleBy } from '../lib/casl-prisma'
import { ability } from '../lib/ability'
import { permittedFieldsOf } from '@casl/ability/extra'

@injectable()
export class PostService {
  private readonly logger

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(PostService.name)
  }

  async findAll() {
    // https://casl.js.org/v6/en/api/casl-ability-extra#permitted-fields-of
    const fields = permittedFieldsOf(ability, 'read', 'Post', {
      fieldsFrom: rule => rule.fields || [],
    })
    const res = await this.prisma.post.findMany({
      select: fields.reduce((acc, cur) => {
        acc[cur] = true
        return acc
      }, {} as any),
    })
    return res
  }
}
