import { Global, Module } from '@nestjs/common'
import { Container } from 'inversify'
import { getServices } from '@flowda-projects/flowda-shared'
import { loadModule } from '../loadModule'

export const servicesContainer = new Container()
loadModule(servicesContainer)

const services = getServices(servicesContainer)

@Global()
@Module({
  providers: services,
  exports: services,
})
export class ServicesModule {
  constructor() {}
}
