import { z } from 'zod';
import type { Prisma } from '@prisma/client-wms';

/////////////////////////////////////////
// HELPER FUNCTIONS
/////////////////////////////////////////


/////////////////////////////////////////
// ENUMS
/////////////////////////////////////////

export const MemberInfoScalarFieldEnumSchema = z.enum(['id','createdAt','updatedAt','isDeleted','name','gender']);

export const PostScalarFieldEnumSchema = z.enum(['id','title','content','published','authorId']);

export const SortOrderSchema = z.enum(['asc','desc']);

export const TaskFormRelationScalarFieldEnumSchema = z.enum(['id','createdAt','updatedAt','isDeleted','taskDefinitionKey','formKey']);

export const TransactionIsolationLevelSchema = z.enum(['ReadUncommitted','ReadCommitted','RepeatableRead','Serializable']);

export const UserProfileScalarFieldEnumSchema = z.enum(['id','createdAt','updatedAt','isDeleted','userId','fullName']);

export const UserScalarFieldEnumSchema = z.enum(['id','createdAt','updatedAt','isDeleted','username','hashedPassword','hashedRefreshToken','status','role']);

export const UserStatusSchema = z.enum(['ACTIVE','FORBIDDEN']);

export type UserStatusType = `${z.infer<typeof UserStatusSchema>}`

export const UserGroupSchema = z.enum(['ADMIN','USER']);

export type UserGroupType = `${z.infer<typeof UserGroupSchema>}`

/////////////////////////////////////////
// MODELS
/////////////////////////////////////////

/////////////////////////////////////////
// TASK FORM RELATION SCHEMA
/////////////////////////////////////////

export const TaskFormRelationSchema = z.object({
  id: z.number().int(),
  createdAt: z.date(),
  updatedAt: z.date(),
  isDeleted: z.boolean(),
  taskDefinitionKey: z.string(),
  formKey: z.string(),
}).openapi({"display_name":""})

export type TaskFormRelation = z.infer<typeof TaskFormRelationSchema>

/////////////////////////////////////////
// MEMBER INFO SCHEMA
/////////////////////////////////////////

export const MemberInfoSchema = z.object({
  id: z.number().int(),
  createdAt: z.date(),
  updatedAt: z.date(),
  isDeleted: z.boolean(),
  name: z.string(),
  gender: z.string(),
}).openapi({"display_name":""})

export type MemberInfo = z.infer<typeof MemberInfoSchema>

/////////////////////////////////////////
// USER SCHEMA
/////////////////////////////////////////

export const UserSchema = z.object({
  status: UserStatusSchema,
  role: UserGroupSchema,
  id: z.number().int(),
  createdAt: z.date(),
  updatedAt: z.date(),
  isDeleted: z.boolean().nullable(),
  username: z.string(),
  hashedPassword: z.string().nullable(),
  hashedRefreshToken: z.string().nullable(),
}).openapi({"display_name":""})

export type User = z.infer<typeof UserSchema>

// USER RELATION SCHEMA
//------------------------------------------------------

export type UserRelations = {
  profile?: UserProfileWithRelations | null;
  Post: PostWithRelations[];
};

export type UserWithRelations = z.infer<typeof UserSchema> & UserRelations

export const UserWithRelationsSchema: z.ZodObject<any> = UserSchema.merge(z.object({
  profile: z.lazy(() => UserProfileWithRelationsSchema).nullable(),
  Post: z.lazy(() => PostWithRelationsSchema).array(),
}))

/////////////////////////////////////////
// USER PROFILE SCHEMA
/////////////////////////////////////////

export const UserProfileSchema = z.object({
  id: z.number().int(),
  createdAt: z.date(),
  updatedAt: z.date(),
  isDeleted: z.boolean().nullable(),
  userId: z.number().int(),
  fullName: z.string(),
})

export type UserProfile = z.infer<typeof UserProfileSchema>

// USER PROFILE RELATION SCHEMA
//------------------------------------------------------

export type UserProfileRelations = {
  user: UserWithRelations;
};

export type UserProfileWithRelations = z.infer<typeof UserProfileSchema> & UserProfileRelations

export const UserProfileWithRelationsSchema: z.ZodObject<any> = UserProfileSchema.merge(z.object({
  user: z.lazy(() => UserWithRelationsSchema),
}))

/////////////////////////////////////////
// POST SCHEMA
/////////////////////////////////////////

export const PostSchema = z.object({
  id: z.number().int(),
  title: z.string(),
  content: z.string().nullable(),
  published: z.boolean(),
  authorId: z.number().int(),
})

export type Post = z.infer<typeof PostSchema>

// POST RELATION SCHEMA
//------------------------------------------------------

export type PostRelations = {
  author: UserWithRelations;
};

export type PostWithRelations = z.infer<typeof PostSchema> & PostRelations

export const PostWithRelationsSchema: z.ZodObject<any> = PostSchema.merge(z.object({
  author: z.lazy(() => UserWithRelationsSchema),
}))
