import { z } from 'zod'
import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'
import { SchemaTransformer } from './schemaTransformer'
import { CustomZodSchemaSymbol, PrismaZodSchemaSymbol } from '../../interfaces/types'

@injectable()
export class SchemaService {
  private readonly logger

  constructor(
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
    @inject('Factory<SchemaTransformer>')
    private modelSchemaFactory: (z: z.AnyZodObject) => SchemaTransformer,
    @inject(PrismaZodSchemaSymbol) private zt: any,
    @inject(CustomZodSchemaSymbol) private czt: any,
  ) {
    this.logger = loggerFactory(SchemaService.name)
  }

  getSchema(): any {
    // console.time('generate schema')
    const schema = Object.keys(this.czt).reduce((acc: any, k) => {
      const e = this.czt[k]
      if (['ZodObject'].indexOf(e.constructor.name) > -1) {
        const transformer = this.modelSchemaFactory(e)
        acc[k] = transformer.buildSchema(k).toSchema()
      } else {
        this.logger.error('Wrong type', k)
      }
      return acc
    }, {})
    // console.timeEnd('generate schema')
    return schema
  }
}
