import { Body, Controller, Get, Param, Post } from '@nestjs/common'
import { TaskService } from '@flowda-projects/wms-services'

@Controller('/tasks')
export class TaskController {
  constructor(private readonly taskService: TaskService) {}

  @Get('/:taskId/form')
  getTaskForm(@Param('taskId') taskId: string) {
    return this.taskService.getTaskForm(taskId)
  }

  @Get('/:taskId')
  getTask(@Param('taskId') taskId: string) {
    return this.taskService.getTask(taskId)
  }

  @Post('/:taskId/complete')
  completeTask(@Param('taskId') taskId: string, @Body() body: any) {
    return this.taskService.complete(taskId, body)
  }
}
