/**
 * This is not a production server yet!
 * This is only a minimal backend to get started.
 */

import { Logger } from '@nestjs/common'
import { NestFactory } from '@nestjs/core'

import { AppModule } from './app/app.module'
import { createProxyMiddleware } from 'http-proxy-middleware'
import { UserService, WMS_ENV } from '@flowda-projects/wms-services'
import { ExtractJwt } from 'passport-jwt'
import { AuthService } from '@flowda-projects/flowda-shared'

async function bootstrap() {
  const app = await NestFactory.create(AppModule)
  app.enableCors()
  const globalPrefix = 'api'
  app.setGlobalPrefix(globalPrefix)

  const user = app.get<UserService>(UserService)
  const auth = app.get<AuthService>(AuthService)

  app.use((req, res, next) => {
    if (req.originalUrl.includes('favicon.ico')) {
      res.status(204).end()
    } else if (req.url.indexOf('/api/camunda/engine-rest/') > -1) {
      const extract = ExtractJwt.fromAuthHeaderAsBearerToken()
      const bearerToken = extract(req)
      if (bearerToken) {
        try {
          const authed = user.verifyAccessToken(bearerToken)
          if (authed) {
            auth.setUID(authed.uid)
            next()
          } else {
            auth.setUID(null)
            res.status(401).json({
              message: '[PROXY] Unauthorized',
            })
          }
        } catch (e) {
          res.status(401).json({
            message: '[PROXY] Unauthorized',
          })
        }
      } else {
        res.status(401).json({
          message: '[PROXY] Unauthorized',
        })
      }
    } else {
      next()
    }
  })

  // Proxy endpoints
  app.use(
    '/api/camunda/engine-rest/',
    createProxyMiddleware({
      target: WMS_ENV.C7_REST_URL,
      changeOrigin: true,
      pathRewrite: {
        [`^/api/camunda/engine-rest/`]: '',
      },
    }),
  )

  const port = process.env.PORT || 3344
  await app.listen(port)
  Logger.log(` Application is running on: http://localhost:${port}/${globalPrefix}`)
}

bootstrap()
