import { Body, Controller, HttpCode, Logger, Post, Request, UseGuards, UsePipes } from '@nestjs/common'
import { UserLocalAuthGuard } from './userLocalAuth.guard'
import { RegisterDto, UserService } from '@flowda-projects/wms-services'
import { ZodValidationPipe } from 'nestjs-zod'
import { UserJwtAuthGuard } from './userJwtAuth.guard'

@Controller('user')
export class UserController {
  private readonly logger = new Logger(UserController.name)

  constructor(private service: UserService) {}

  @Post('register')
  @UsePipes(ZodValidationPipe)
  register(@Body() dto: RegisterDto) {
    return this.service.register(dto)
  }

  @UseGuards(UserLocalAuthGuard)
  @Post('login')
  @HttpCode(200)
  login(@Request() req) {
    return req.user
  }

  @UseGuards(UserJwtAuthGuard)
  @Post('logout')
  logout(@Request() req) {
    //  token
    //  refresh token
    //  blacklist access_token  10min
    return req.user
  }
}
