import { Controller, Get, Query } from '@nestjs/common'

import { SchemaService } from '@flowda-projects/flowda-shared'
import { PostService, UserService } from '@flowda-projects/wms-services'

@Controller('/apps')
export class AppController {
  constructor(
    private readonly schema: SchemaService,
    private readonly post: PostService,
    private readonly user: UserService,
  ) {}

  @Get('/__hi')
  hi() {
    return { hi: 'world' }
  }

  @Get('/getSchema')
  getSchema() {
    return this.schema.getSchema()
  }

  @Get('/getPosts')
  getPosts() {
    return this.post.findAll()
  }

  @Get('/findByUsername')
  findByUsername(@Query('username') username: string) {
    return this.user.findByUsername(username)
  }
}
