import { parseEnv } from 'znv'
import { z } from 'zod'

export const WMS_ENV = parseEnv(process.env, {
  TEST_ENV: z.string().optional(),
  DATABASE_URL: z.string().min(1),
  REFRESH_TOKEN_SECRET: z.string().min(1),
  REFRESH_TOKEN_EXPIRE: z.number().default(7 * 24 * 60 * 60), // 1 week
  ACCESS_TOKEN_SECRET: z.string().min(1),
  ACCESS_TOKEN_EXPIRE: z.number().default(25 * 60),
  C7_REST_URL: z.string().min(1),
})
