import { Module } from '@nestjs/common'
import { APP_FILTER } from '@nestjs/core'

import { AppController } from './app.controller'
import { ServicesModule } from '../services/services.module'
import { AppExceptionFilter } from '@flowda-projects/flowda-shared-node'
import { TaskController } from './task.controller'
import { UserController } from '../user/user.controller'
import { UserLocalStrategy } from '../user/userLocal.strategy'
import { UserJwtStrategy } from '../user/userJwt.strategy'
import { ProcessController } from './process.controller'
import { DataController } from './data.controller'

@Module({
  imports: [ServicesModule],
  controllers: [AppController, DataController, TaskController, UserController, ProcessController],
  providers: [
    {
      provide: APP_FILTER,
      useClass: AppExceptionFilter,
    },
    UserLocalStrategy,
    UserJwtStrategy,
  ],
})
export class AppModule {}
