import { Controller, Logger, Param, Post, Req, UseGuards } from '@nestjs/common'
import { TaskService } from '@flowda-projects/wms-services'
import { UserJwtAuthGuard } from '../user/userJwtAuth.guard'

@Controller('/process')
export class ProcessController {
  private readonly logger = new Logger(ProcessController.name)

  constructor(private readonly taskService: TaskService) {}

  @UseGuards(UserJwtAuthGuard)
  @Post('/:key/start')
  startTask(@Req() req, @Param('key') key: string) {
    this.logger.log(req.user)
    return this.taskService.start(key, req.user.uid)
  }
}
