import { inject, injectable } from 'inversify'
import { PrismaClientSymbol } from '@flowda-projects/flowda-shared'
import * as db from '@prisma/client-wms'
import { z } from 'zod'
import { createZodDto } from 'nestjs-zod'
import { UserError } from '../lib/error-code'
import * as bcrypt from 'bcrypt'
import type { JwtPayload } from 'jsonwebtoken'
import * as jwt from 'jsonwebtoken'
import { WMS_ENV } from '../lib/wms-env'
import axios from 'axios'
import { LoggerService, UnauthorizedException } from '@nestjs/common'

export const registerSchema = z.object({
  username: z.string(),
  password: z.string(),
})

export class RegisterDto extends createZodDto(registerSchema) {}

@injectable()
export class UserService {
  private readonly logger

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(UserService.name)
  }

  async register(dto: RegisterDto) {
    const user = await this.prisma.user.findFirst({
      where: {
        username: dto.username,
      },
    })
    if (user) {
      this.logger.warn('User exist:' + dto.username)
      throw new UserError.UserExist()
    }
    //  c7
    // todo:  mock env
    if (WMS_ENV.TEST_ENV !== 'yes') {
      try {
        await axios.post(WMS_ENV.C7_REST_URL + `/user/create`, {
          profile: {
            id: dto.username,
          },
          credentials: {
            password: dto.password,
          },
        })
      } catch (e) {
        this.logger.error('call c7 failed:/user/create:' + dto.username)
        throw e
      }
    }
    const hashedPassword = await bcrypt.hash(dto.password, 10)
    const aUser = await this.prisma.user.create({
      data: {
        username: dto.username,
        hashedPassword: hashedPassword,
        hashedRefreshToken: null,
        status: db.UserStatus.ACTIVE,
      },
    })
    return {
      id: aUser.id,
      username: aUser.username,
    }
  }

  async validate(username: string, password: string) {
    const user = await this.prisma.user.findFirst({
      where: {
        username: username,
      },
    })

    if (!user) {
      throw new UnauthorizedException({ reason: 'User does not exist', username })
    }

    if (!user.hashedPassword) {
      throw new UnauthorizedException({ reason: 'Password is not initialized', username })
    }

    const match = await bcrypt.compare(password, user.hashedPassword)
    if (!match) {
      throw new UnauthorizedException({ reason: 'Username and password is not matched', username })
    }

    const payload = { uid: user.id }
    const rt = this.generateJwt(payload, WMS_ENV.REFRESH_TOKEN_SECRET, WMS_ENV.REFRESH_TOKEN_EXPIRE)
    user.hashedRefreshToken = rt.token
    await this.prisma.user.update({
      where: { id: user.id },
      data: user,
    })

    const at = this.generateJwt(payload, WMS_ENV.ACCESS_TOKEN_SECRET, WMS_ENV.ACCESS_TOKEN_EXPIRE)

    this.logger.log('validate pass:' + username)

    return {
      username: user.username,
      refresh_token: rt.token,
      access_token: at.token,
    }
  }

  generateJwt(payload: any, secret: string, expires: number) {
    const exp0 = Date.now() + expires * 1000
    const token = jwt.sign(
      {
        ...payload,
        ...{ exp0 },
      },
      secret,
      {
        expiresIn: `${expires}s`,
      },
    )
    const decode = jwt.decode(token) as JwtPayload
    return {
      token: token,
      iat: decode.iat as number,
      exp: decode.exp as number,
    }
  }

  verifyAccessToken(at: string) {
    return jwt.verify(at, WMS_ENV.ACCESS_TOKEN_SECRET) as JwtPayload
  }

  async findByUsername(username: string) {
    return this.prisma.user.findFirstOrThrow({
      where: {
        username,
      },
    })
  }
}
